
import { Injectable } from '@angular/core'
import { TranslateService } from '@ngx-translate/core';
import { LoadingController } from 'ionic-angular';

import { Util } from '../common/util'



@Injectable()
export class Global {

    private tr_keys: string[] = [
        'TEXT_LOADING',
        'TEXT_SAVING',
        
        "TITLE_TIMELINE",
        "TITLE_STATS",
        "TITLE_MISSIONS",
        "TITLE_MORE",
    ]
    public tr: any = null

    private tr_waiting: Array<any> = []

    constructor(
        private loading_ctrl: LoadingController,
        private translate_service: TranslateService,
    ) {
        this.init_translate()
    }

    public start_loading(message: string): any {
        let loading = this.loading_ctrl.create({
            content: message
        })
        loading.present()
        return loading
    }

    public init_translate(): void {
        this.translate_service.get(this.tr_keys).subscribe(values => {
            this.tr = values
            for (let callback of this.tr_waiting) {
                callback()
            }
            this.tr_waiting = []
        });
    }

    public wait_for_translation(): Promise<any> {
        let self = this
        return new Promise(function (resolve, reject) {
            if (self.tr) {
                resolve()
            }
            else {
                self.tr_waiting.push(resolve)
            }
        })
    }
    
}



