import { Component, ViewChild } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { NavController, Tab, Tabs } from 'ionic-angular';

import { TimelinePageComp } from '../components'

import { Global } from '../../models/models'

@Component({
    selector: 'comp-tabs',
    templateUrl: 'tabs.html'
})
export class TabsComp {
    @ViewChild('tabs_comp') private tabs_comp: Tabs

    public tabs: {
        title: string, icon: string, component: any
    }[] = [
        // {title: 'TITLE_TIMELINE', icon: 'pulse', component: TimelinePageComp},
        // {title: 'TITLE_STATS', icon: 'stats', component: StatsPageComp},
        // {title: 'TITLE_MISSIONS', icon: 'list-box', component: MissionsPageComp},
        // {title: 'TITLE_MORE', icon: 'more', component: MorePageComp},
        {title: 'TITLE_TIMELINE', icon: 'pulse', component: TimelinePageComp},
        {title: 'TITLE_STATS', icon: 'stats', component: TimelinePageComp},
        {title: 'TITLE_MISSIONS', icon: 'list-box', component: TimelinePageComp},
        {title: 'TITLE_MORE', icon: 'more', component: TimelinePageComp},
    ]

    constructor(
        private nav_ctrl: NavController,
        private translate_service: TranslateService,
        private global: Global,
    ) {
        this.init_translate()
    }

    private init_translate() {
        let self = this
        this.global.wait_for_translation().then(function () {
            for (const tab of self.tabs) {
                tab.title = self.global.tr[tab.title]
            }
        })
    }

    public change_tab(tab_or_index: number | Tab) {
        this.tabs_comp.select(tab_or_index)
    }
}




