
import { Injectable } from '@angular/core'
import { Storage } from '@ionic/storage';
import { TranslateService } from '@ngx-translate/core';

import { Util } from '../common/util'
import { Global } from './models'

@Injectable()
export class StorageManager {

    constructor(
        private storage: Storage,
        private global: Global,
        private translate_service: TranslateService,
    ) {
        
    }

    public load(key: string): Promise<any> {
        let loading = this.global.start_loading(this.global.tr.TEXT_LOADING)
        return this.storage.get(key).then(function (value) {
            loading.dismiss()
            return value
        })
    }

    public save(key: string, value: any): Promise<any> {
        let loading = this.global.start_loading(this.global.tr.TEXT_SAVING)
        return this.storage.set(key, value).then(function () {
            loading.dismiss()
        })
    }

}



